export default {
	name: "basics",
	data() {
		return {
			orderMethod: 0,
			filterMethod: 0,
			modalName: null,
			cardCur: 0,
			swiperList: [{
				id: 0,
				type: 'image',
				url: '../../static/girls/1.jpg'
			}, {
				id: 1,
				type: 'image',
				url: '../../static/girls/2.jpg',
			}, {
				id: 2,
				type: 'image',
				url: '../../static/girls/3.jpg'
			}],
			dotStyle: false,
		};
	},
	methods:{
		goSearch(){
			uni.navigateTo({
				url: '../search/search'
			})
		},
		book(e){
			this.modalName = null
			uni.navigateTo({
				url: "../people-detail/people-detail?status=" + e.currentTarget.dataset.key
			})
		},
		viewDetail(){
			this.modalName = "book"
		},
		hideModal(){
			this.modalName = null
		},
		changeModal(e){
			this.modalName = e.currentTarget.dataset.name
		},
		showLocation(){
			
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
	},
	onShow() {
		console.log("success")
	}
}
